<template>
  <v-card>
    <v-card-title>
      每日任務
      <v-spacer></v-spacer>
      <small>
        <div>
          {{ date }} {{ role=="teacher" ? className :''}}
          <v-icon @click="calendarOpen=!calendarOpen">mdi-calendar</v-icon>
        </div>
      </small>
    </v-card-title>
    <v-card-text>
      <v-expand-transition>
        <v-date-picker v-show="calendarOpen" v-model="date" full-width></v-date-picker>
      </v-expand-transition>
      <v-btn-toggle v-if="role=='teacher'" dense v-model="mode" mandatory>
        <v-btn value="timeline">
          <v-icon>mdi-clock</v-icon>
        </v-btn>
        <v-btn value="list">
          <v-icon>mdi-format-align-justify</v-icon>
        </v-btn>
      </v-btn-toggle>
      <v-timeline v-if="mode =='timeline'">
        <v-timeline-item
          v-for="task in tasks.filter(t=>t.role.includes(role))"
          :key="task.title"
          :color="task.color"
          :small="task.small"
          :class="'scrollToHere'"
        >
          <v-card :color="task.color">
            <v-card-title>
              <span class="text-body-1 white--text">{{ task.time }} {{ task.title }}</span>
            </v-card-title>
            <v-card-text>
              <v-btn icon :to="task.route">
                <v-icon>{{ task.icon }}</v-icon>
              </v-btn>
              <br />
              <span>{{ task.detail }}</span>
            </v-card-text>
          </v-card>
        </v-timeline-item>
      </v-timeline>
      <v-list flat v-if="mode == 'list'">
        <v-list-item-group color="primary">
          <v-list-item v-for="(task,i) in tasks" :key="i" :to="task.route">
            <v-list-item-icon>
              <v-icon v-text="task.icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="task.title"></v-list-item-title>
              <v-list-item-subtitle v-html="task.detail"></v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  computed: {
    role() {
      return this.$store.getters.role;
    },
  },
  data() {
    let todayDate = new Date().toLocaleDateString().replace(/\//g, "-");
    return {
      mode: "timeline",
      className: "小熊幼幼班",
      date: todayDate,

      calendarOpen: false,

      tasks: [
        {
          title: "測評",
          detail: "",
          icon: "mdi-pencil",
          color: "red",
          role: ["parent"],
        },
        {
          time: "08:00",
          title: "點名",
          detail: "",
          icon: "mdi-table-edit",
          color: "red",
          route: "/quest/rollcall",
          role: ["teacher"],
        },
        {
          time: "10:00",
          title: "課程",
          detail: "繪本 小熊愛唱歌",
          icon: "mdi-book-open-page-variant",
          color: "orange",
          route: "/quest/check",
          role: ["teacher", "parent"],
        },
        {
          time: "11:30",
          title: "餵奶",
          detail: "",
          icon: "mdi-food-variant",
          color: "red",
          route: "/quest/childcare",
          role: ["teacher", "parent"],
        },
        {
          time: "13:00",
          title: "保育任務",
          detail: "",
          icon: "mdi-robot",
          color: "blue",
          small: true,
          route: "/quest/childcare",
          role: ["teacher", "parent"],
        },
        {
          time: "14:30",
          title: "團康活動",
          detail: "",
          icon: "mdi-cake",
          color: "green",
          role: ["teacher"],
        },
        {
          time: "16:30",
          title: "清潔",
          detail: "",
          icon: "mdi-leaf",
          color: "green",
          route: "/quest/cleanup",
          role: ["teacher"],
        },
        {
          time: "18:30",
          title: "個別觀察訪談",
          detail: "",
          icon: "mdi-phone",
          color: "warning",
          route: "/quest/interview/observation",
          role: ["teacher"],
        },
      ],
    };
  },
  created() {
    let now = new Date();
    let index = this.tasks.findIndex(
      (task) => new Date(this.date + " " + task.time) - now > 0
    );
    this.tasks.splice(index, 0, {
      time: "",
      title: "缺席訪談",
      detail: "",
      icon: "mdi-phone",
      color: "blue",
      small: true,
      scroll: true,
      route: "/quest/interview/absent",
      role: ["teacher"],
    });
  },
  mounted() {
    // this.$vuetify.goTo(".scrollToHere");
    if (this.role == "teacher") this.mode = "list";
  },
};
</script>

